html, body {
  width: 100%;
  height: 100%;
}

body {
  background: #333;
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: Arial, san-serrif;
}

.wrapper {
  width: 100%;
  height: 100%;
  background: #000000;
  background: -moz-radial-gradient(center, ellipse cover, #000000 43%, #333333 100%);
  background: -webkit-radial-gradient(center, ellipse cover, #000000 43%,#333333 100%);
  background: radial-gradient(ellipse at center, #000000 43%,#333333 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#333333',GradientType=1 );
}

@size: 200px;
@dotSize: 20px;

@time: 2s;
@delay: 0.1s;
@padding: 20px;
@iterations: 35;

.loader{
  position: absolute;
  width: @size;
  height: @size;
  left: 75%;
  top: 0%;
  transform: translate(-50%, -50%) rotateZ(45deg);

  div{
    position: absolute;
    left: 0;
    width: @dotSize;
    height: @dotSize;
    background: #FFF;
    border-radius: @dotSize;
    opacity: 0;
  }
}

.dna-loop (@i) when (@i > 0) {

  .loader div:nth-child(@{i}) {
    top: @i * @padding;
    background: #FFF;
    animation:dna_rotate @time @i * @delay infinite ease-in-out;
    & when (mod(@i, 2) = 0) {
      background: #990fff;
      animation:dna_rotate @time @i * @delay + @time / 2 infinite ease-in-out;
    }
  }
  .dna-loop (@i - 1)
}
.dna-loop (@iterations);

@keyframes dna_rotate {
  0%   {
    opacity: 1;
    transform: scale(1);
    left:20%;  z-index: 0;
  }
  25%  {
    opacity: 1;
    transform: scale(1.75);
  }
  50%  {
    opacity: 1;
    left:80%; z-index: 1;
    box-shadow: 0 0 6px 3px rgba(255,0,255,0.5);
    transform: scale(1);
  }
  75%  {
    opacity: 1;
    transform: scale(0.25);
  }
  100% {
    opacity: 1;
    left:20%;  z-index: 0;
    transform: scale(1);
  }
}
