}
@keyframes dna_rotate {
  0% {
    opacity: 1;
    transform: scale(1);
    left: 20%;
    z-index: 0;
  }
  25% {
    opacity: 1;
    transform: scale(1.75);
  }
  50% {
    opacity: 1;
    left: 80%;
    z-index: 1;
    box-shadow: 0 0 6px 3px rgba(255, 0, 255, 0.5);
    transform: scale(1);
  }
  75% {
    opacity: 1;
    transform: scale(0.25);
  }
  100% {
    opacity: 1;
    left: 20%;
    z-index: 0;
    transform: scale(1);
  }
}
